<template>
  <Card>
    <p slot="title">
      应用新增
    </p>
    <Form
      ref="formValidate"
      :model="formValidate"
      :rules="ruleValidate"
      :label-width="80"
      style="width: 500px;margin: 0 auto"
    >
      <FormItem label="应用code" prop="code">
        <Input v-model="formValidate.code" placeholder="请输入"></Input>
      </FormItem>
      <FormItem label="应用编号" prop="name">
        <Input v-model="formValidate.name" placeholder="请输入"></Input>
      </FormItem>
      <FormItem label="应用地址" prop="downloadUrl">
        <Upload
          :action="upload_url"
          :data="fileData"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :on-progress="handleProgress"
          :on-error="handleError"
          :before-upload="onBefore"
          :on-success="handleSuccess"
          :default-file-list="defaultList"
        >
          <Button icon="ios-cloud-upload-outline">上传文件</Button>
        </Upload>
      </FormItem>
      <FormItem label="应用概要" prop="intro">
        <Input
          v-model="formValidate.intro"
          type="textarea"
          :autosize="{ minRows: 2, maxRows: 5 }"
          placeholder="请输入"
        ></Input>
      </FormItem>
      <FormItem label="应用内容" prop="info">
        <Input
          v-model="formValidate.info"
          type="textarea"
          :autosize="{ minRows: 2, maxRows: 5 }"
          placeholder="请输入"
        ></Input>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleSubmit('formValidate')"
          >提交</Button
        >
        <!-- <Button @click="handleReset('formValidate')" style="margin-left: 8px"
          >重置</Button
        > -->
      </FormItem>
    </Form>
  </Card>
</template>
<script>
import { appVersionNew } from '@/api/application.js'
import { SERVER_CODE, SERVER_URL } from '@/utils/constants.js'
export default {
  data() {
    return {
      fileData: { fileType: 1004, project: 'PORT', password: 'heihei' },
      defaultList: [],
      upload_url: SERVER_URL.UPLOAD_URL,
      formValidate: {
        code: '',
        downloadUrl: '',
        info: '',
        intro: '',
        name: ''
      },
      ruleValidate: {
        code: [
          {
            required: true,
            message: '不能为空',
            trigger: 'blur'
          }
        ],
        name: [
          {
            required: true,
            message: '不能为空',
            trigger: 'blur'
          }
        ],
        downloadUrl: [
          {
            required: true,
            message: '不能为空',
            trigger: 'blur'
          }
        ],
        intro: [
          {
            required: true,
            message: '不能为空',
            trigger: 'blur'
          }
        ],
        info: [
          {
            required: true,
            message: '不能为空',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  mounted() {},
  activated() {
    this.formValidate = {
      code: '',
      downloadUrl: '',
      info: '',
      intro: '',
      name: ''
    }
    this.defaultList = []
  },
  methods: {
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          appVersionNew(this.formValidate)
            .then(({ code, msg }) => {
              if (code === SERVER_CODE.CODE_200) {
                this.$Message.success('应用新增成功！')
                this.$store.state.applicationStatus = true
                setTimeout(() => {
                  this.$router.go(-1)
                }, 2000)
              } else {
                this.$Message.info(msg)
                console.log('应用新增失败')
              }
            })
            .catch(error => {
              console.log(error)
            })
            .finally(() => {})
        }
      })
    },
    onBefore(file) {
      let exceededNum = file.name.lastIndexOf('.')
      let exceededStr = file.name
        .substring(exceededNum + 1, file.name.length)
        .trim()
      if (exceededStr != 'apk' && exceededStr != 'ipa') {
        this.$Notice.warning({
          title: '文件格式错误',
          desc: file.name + '文件格式不正确, 请选择 apk ipa'
        })
        return false
      }
      if (file.size > 102400 * 2000) {
        this.$Notice.warning({
          title: '文件大小错误',
          desc: file.name + '文件大小太大，超过200M'
        })
        return false
      }
      return true
    },
    handleError() {
      this.$Message.info('上传失败！')
    },
    handleRemove() {
      //删除文件
      this.formValidate.downloadUrl = ''
    },
    handleProgress() {
      //文件上传时的钩子
    },
    handlePreview(file) {
      //点击已上传的文件链接时的钩子
      location.href = file.url
    },
    handleSuccess(res, file) {
      //上传成功只保留一份文件
      this.defaultList = []
      let data = { name: file.name, url: res.data }
      this.defaultList.push(data)
      this.formValidate.downloadUrl = res.data
      this.$Message.success('上传成功！')
    },
    handleReset(name) {
      this.$refs[name].resetFields()
    }
  }
}
</script>
